<template>
  <div class="wrap wrap-export-import">
    <!--面包导航-->
    <div class="nav-bar clear-fix">
      <el-breadcrumb separator=">" class="fl-l">
        <el-breadcrumb-item>商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>商品模板下载与导入</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-form ref="form" label-width="150px" class="load-import" :inline="true">
        <el-form-item label="批量上传模板" class="load-template">     
          <el-button type="primary" @click="getTemplate" size="mini">下载</el-button>
          <a href="/goods/upload-help" target="_blank" class="export-tip">帮助中心</a>
        </el-form-item>
        <el-form-item label="导入商品">
          <el-upload class="upload-demo" action="/proxy/goods-upload/api/goods/upload" accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel" :on-success="success" :on-error="error">
            <el-button size="mini" type="primary">导入</el-button>
            <span slot="tip" class="el-upload__tip">
              <a class="upload-tip" href="http://s1.pic.intmall.net/file/vendors/goods_import_rules.pdf" download="goods_import_rules.pdf">批量上传操作指引</a>
              只能导入excel文件
            </span>
          </el-upload>

        </el-form-item>
      </el-form>

    </div>

    <div class="goodsImport-info">
      <p class="txt">商品导入信息</p>
      <!--搜索-->
      <div class="search report">
        <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
          <el-form-item label="文件名称">
            <el-input v-model="searchForm.fileName" size="small"></el-input>
          </el-form-item>
          <el-form-item label="处理状态">
            <el-select v-model="searchForm.uploadStatus" size="small" clearable>
              
              <el-option value="0" label="等待处理"></el-option>
              <el-option value="1" label="数据处理中"></el-option>
              <el-option value="2" label="导入完成 "></el-option>
              <el-option value="3" label="取消 "></el-option>
              <el-option value="4" label="上传失败 "></el-option>
            </el-select>
          </el-form-item>
          <el-form-item class="search-btn">
            <el-button type="primary" @click="search_smt">查询</el-button>
            <el-button type="primary" @click="refresh">
              <i class="iconfont icon-refresh"></i>刷新
            </el-button>
          </el-form-item>
        </el-form>
      </div>
      <!--列表-->
      <el-table :data="goodsList" stripe border style="width: 100%" v-loading="loading2" class="report" max-height="520" :tooltip-effect="'light'" highlight-current-row >
        <el-table-column label="上传编号" align="left" prop="id" show-overflow-tooltip width="90" fixed="left"> </el-table-column>
        <el-table-column label="文件名称" align="center" prop="fileName" show-overflow-tooltip min-width="150"> </el-table-column>
        <el-table-column label="处理状态" align="center" width="180" prop="uploadStatus" show-overflow-tooltip>
          <template slot-scope="scope">{{scope.row.uploadStatus|uploadStatus }}</template>
        </el-table-column>
        <el-table-column label="上传时间" align="center" width="180" prop="uploadTime">
        </el-table-column>
        <el-table-column label="处理完成时间" align="center" width="160" prop="completeTime">
        </el-table-column>
        <el-table-column label="SKU成功条数" align="center" width="120" prop="skuUploadCount" show-overflow-tooltip> </el-table-column>
        <el-table-column label="SPU成功条数" align="center" width="120" prop="spuUploadCount" show-overflow-tooltip> </el-table-column>
        <el-table-column label="excel大小(kb)" align="center" width="120" prop="excelFileSize" show-overflow-tooltip> </el-table-column>
        <el-table-column label="上传失败信息" align="left" width="150" prop="uploadErrorMsg" show-overflow-tooltip fixed="right">
          <template slot-scope="scope">
            <span class="action" @click="showMsg(scope.row.uploadErrorMsg)">描述信息</span>
            <span class="action" @click="showProgressImg(scope.row)" v-if="scope.row.uploadStatus==1||scope.row.uploadStatus==2||scope.row.uploadStatus==4">图片信息</span>
            <!-- <el-button type="danger" @click="showMsg(scope.row.uploadErrorMsg)" size="mini">查看</el-button> -->
          </template>
        </el-table-column>
        <el-table-column label="操作" align="left" width="120" fixed="right">
          <template slot-scope="scope">
            <span class="action" @click="doAction(scope.row)" v-if="scope.row.uploadStatus==0">取消</span>
            <span class="action" @click="showProgress(scope.row)" v-if="scope.row.uploadStatus==1">进度</span>
          </template>
        </el-table-column>

      </el-table>
      <!-- //上传失败信息 -->
      <el-dialog title="上传失败信息" :visible.sync="dialogMsg" class="dialog-load-import">
        <el-table :data="msgFaile" max-height="520" stripe :tooltip-effect="'light'" highlight-current-row>
          <el-table-column type="index" width="70">
          </el-table-column>
          <el-table-column property="msg" label="异常信息"></el-table-column>
        </el-table>
      </el-dialog>

      <!-- //图片失败信息 -->
      <el-dialog title="上传图片信息" :visible.sync="dialogMsgImg" class="dialog-load-import">
        <div style="color:#FA4640">图片处理耗时较长，查看异常信息需实时刷新</div>
        <el-table :data="msgImgFaile" max-height="520" stripe :tooltip-effect="'light'" highlight-current-row>
          <el-table-column type="index" width="70">
          </el-table-column>
          <el-table-column property="msg" label="异常信息"></el-table-column>
        </el-table>
      </el-dialog>

      <!-- 分页 -->
      <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
        <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </div>
    <el-dialog title="进度" :visible.sync="dialogProgress" size="tiny" @open="dialogProgressOpen"  @close="dialogProgressClose">
      <span>{{progressMsg}}</span>
    </el-dialog>
  </div>
</template>

<style lang="less" >
.wrap {
  padding-right: 40px;
}
.wrap-export-import{
  .action{
    margin:0 5px ;
    cursor: pointer;
    color:#409EFF;
  }
}
.load-import {
  font-size: 12px;
  
  .el-form-item.load-template{
    margin-left:-65px;
  }
  .el-form-item__label{
    font-size: 12px;
  }
  a{
    font-size: 12px;
  }
}

.table-msg {
  max-height: 500px;
  overflow-y: auto;
}

.upload-tip,.export-tip {
  color: #d73435;
  margin: 0 15px;
}
</style>



<script>
import format from "date-format";
export default {
  data() {
    return {
      
      props: {
        value: "catId",
        label: "catName"
      },
      cateId: [],
      cateId2: [],
      msgList: [],
      skuUploadCount: 0,
      spuUploadCount: 0,
      loading: false,
      loading2: false,
      searchForm: {
        fileName: "",
        uploadStatus: "",
        page: 1,
        rows: 10
      },
      goodsList: [],
      total: 0,
      //上传失败信息
      dialogMsg: false,
      msgFaile: [],
      //图片失败信息
      dialogMsgImg:false,
      msgImgFaile: [],

      //进度信息
      dialogProgress:false,
      progressMsg: "",
      progressId:'',
      progressSetInterval:'',
      //运费模板
      freightList:[]
    };
  },
  filters: {
    dateFormat(date) {
      return date ? format("yyyy-MM-dd hh:mm", new Date(date)) : "";
    },
    uploadStatus(val) {
      switch (val) {
        case 0:
          return "等待处理";
          break;
        case 1:
          return "数据处理中";
          break;
        case 2:
          return "导入完成";
          break;
        case 3:
          return "取消";
          break;
        case 4:
          return "上传失败";
          break;
      }
    }
  },
  methods: {
  
    //上传成功
    success(response, file, fileList) {
      this.msgList = [];
      if (response.head.code == "10000") {
        this.$message({
          type: "success",
          message: "导入完成，详细信息请点击商品导入列表“查看”按钮"
        });
        this.getGoodsList();
      } else {
        for (let i in fileList) {
          if (file.uid == fileList[i].uid) {
            fileList.splice(i, 1);
          }
        }
        this.$message({
          type: "error",
          message: response.body.sub_msg
        });
      }
    },
    error(err, file, fileList) {
      this.$message({
        message: "上传失败",
        type: "warning"
      });
    },

    //导入信息列表
    getGoodsList() {
      this.loading2 = true;
      this.searchForm.catId = this.cateId2[this.cateId2.length - 1];
      this.axios
        .post(
          this.myConfig.code.proxy + "/goods-upload/api/goods/upload/info",
          { body: this.searchForm }
        )
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.loading2 = false;
            this.goodsList = data.body.rows;
            this.total = data.body.total;
          }
        });
    },
    //分页
    handleCurrentChange(val) {
      this.searchForm.page = val;
      this.getGoodsList();
    },

    search_smt() {
      this.searchForm.page = 1;
      this.getGoodsList();
    },
    //刷新
    refresh() {
      this.getGoodsList();
    },

    //查看错误信息
    showMsg(data) {
      this.msgFaile = [];
      for (let i in data) {
        this.msgFaile.push({ msg: data[i] });
      }
      this.dialogMsg = true;
    },
    //取消
    doAction(row) {
      this.axios
        .post(
          this.myConfig.code.proxy + "/goods-upload/api/goods/upload/cancel",
          { body: { id: row.id } }
        )
        .then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.$message({
              message: "取消成功",
              type: "success"
            });
            this.getGoodsList();
          } else {
            this.$message({
              message: "取消失败",
              type: "warning"
            });
          }
        });
    },
    //查看进度
    showProgress(row) {
      this.progressId=row.id;
      this.dialogProgress=true;
      this.getProgressMsg();    
    },
    //定时请求
    dialogProgressOpen(){
      this.progressSetInterval=setInterval(()=>{
        this.getProgressMsg();
      },2000)
    },
    dialogProgressClose(){
      clearInterval(this.progressSetInterval);
    },
    //获取进度信息
    getProgressMsg() {
      this.axios.post(
          this.myConfig.code.proxy + "/goods-upload/api/goods/upload/plan",
          { body: { id: this.progressId} }
        ).then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            this.progressMsg = data.body.message;
          }
        });
    },

    //图片信息
    getImgMsg() {
      this.axios.post(
          this.myConfig.code.proxy + "/goods-upload/api/goods/down/img/plan",
          { body: { id: this.progressId} }
        ).then(response => {
          let data = response.data;
          if (data.head.code == "10000") {
            let tempArr=[];
            for(let i in data.body.msgRows){
              tempArr.push({msg:data.body.msgRows[i]})
            }
            this.msgImgFaile = tempArr;
          }
        });
    },
    showProgressImg(row){
      this.progressId=row.id;
      this.dialogMsgImg=true;
      this.getImgMsg();
    },
    getTemplate(){
        
        window.open("/proxy/goods-upload/api/goods/down/template");
    },    
  },
  created() {
    this.getGoodsList();
  }
};
</script>




